export const getInitialData = () => {
  let userData = localStorage.getItem('logInUserData') ? JSON.parse(localStorage.getItem('logInUserData')) : {} 
  return async dispatch => {
    await axios.get("/backendapi/nominee/list/"+userData._id).then(response => {
      dispatch({ type: "GET_ALL_DATA", data: response.data })
    })
  }
}

